<template>
  <div class="stat">
    <md-list v-if="data.monthly_list.length">
      <md-list-item
        v-for="list in data.monthly_list"
        :key="list.id"
        :class="list.color"
      >
        <p class="own-statisctic-title">
          <b>{{ list.name }}</b
          ><br /><small>({{ formatedPrice(list.price) }} Ft)</small>
        </p>
        <p
          v-bind:style="{
            background:
              'linear-gradient(to right, ' +
              list.color +
              ' ' +
              list.percent +
              '%,' +
              list.color +
              ' ' +
              list.percent +
              '%,#f4f4f4 0%,#f4f4f4 100%)'
          }"
          class="md-list-item-text own-statistic"
        >
          {{ list.percent }}%
        </p>
      </md-list-item>
      <md-list-item class="total-box">
        <p class="own-statisctic-title text-light"><b>Összesen: </b></p>
        <p class="total-text text-light">
          {{ formatedPrice(data.full_price[0].price) }} Ft
        </p>
      </md-list-item>
    </md-list>
    <div v-else>
      <p class="no-result">Ebben a hónapban nem történt adatrögzés.</p>
      <p class="no-result-icon">
        <md-icon class="no-result-icon-format"
          >sentiment_very_dissatisfied</md-icon
        >
      </p>
      <p class="no-result-bottom">Próbálja meg másik dátummal!</p>
    </div>
    <div class="fixed-footer-element">
      <md-datepicker v-model="selectedDate" />
      <md-button
        v-on:click="getNewItemData(selectedDate)"
        class="md-icon-button md-raised md-primary"
      >
        <md-icon>search</md-icon>
      </md-button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "home",

  data() {
    return {
      selectedDate: new Date(),
      data: []
    };
  },
  mounted() {
    axios
      .post(process.env.VUE_APP_GET_STAT_API, JSON.stringify({ list: true }))
      .then(response => (this.data = response.data))
      .catch(err => console.log(err));
  },
  methods: {
    formatedPrice(price) {
      return new Number(price).toLocaleString("hu-HU");
    },
    getNewItemData(date) {
      axios
        .post(
          process.env.VUE_APP_GET_STAT_API,
          JSON.stringify({ loadThisDate: this.formatDate(date) })
        )
        .then(response => (this.data = response.data))
        .catch(err => console.log(err));
    },
    formatDate(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    }
  },
  computed: {}
};
</script>
<style scoped>
.stat {
  padding-top: 55px;
  padding-bottom: 70px;
}
.own-statistic {
  border-radius: 5px;
  border: 1px solid black;
  font-weight: 900;
  width: 100%;
  text-align: center;
  display: block;
}
.own-statisctic-title {
  display: block;
  text-align: left;
  width: 30vw;
}
.fixed-footer-element {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  bottom: 0;
  background: white;
  z-index: 999;
  width: 100%;
}
.md-datepicker {
  width: 70%;
}
.total-text {
  font-weight: bold;
}
.total-box {
  background: #5394ff;
  padding-right: 5%;
  padding-left: 5%;
}
.text-light {
  color: white;
}
.no-result {
  margin-top: 50px;
  margin-left: 10px;
  margin-right: 10px;
  line-height: 30px;
  text-align: center;
  font-size: 20pt;
}
.no-result-icon {
  text-align: center;
}
.no-result-icon-format {
  font-size: 11rem !important;
  margin-top: 70px;
  color: #5394ff !important;
}
.no-result-bottom {
  margin-top: 100px;
  margin-bottom: 30px;
  margin-left: 10px;
  margin-right: 10px;
  line-height: 30px;
  text-align: center;
  font-size: 20pt;
}
</style>
